.ar-icon {
  position: fixed;
  z-index: 3;
  background: none;
  border: none;
  padding: 3px;
  display: inline-flex;
}

.ar-icon-sound {
  right: 10px;
  top: 10px;
}

.ar-icon-wave {
  right: 55px;
  top: 10px;
}

.ar-scale-button {
  position: fixed;
  z-index: 3;
  background: rgba(0, 0, 0, .3);
  width: 32px;
  height: 32px;
  text-align: center;
  color: #fff;
  font-size: 24px;
  border: 1px solid #fff;
  border-radius: 3px;
}

.ar-scale-button--minus {
  right: 10px;
  bottom: 10px;
}

.ar-scale-button--plus {
  right: 10px;
  bottom: 52px;
}