.sound-wave-bar {
  width: 3px;
  transition: .3s height, .3s y;
}

.sound-wave-bar-1 {
  height: 11px;
  y: 18.5px;
}

.sound-wave-bar-2 {
  height: 31px;
  y: 8.5px;
}

.sound-wave-bar-3 {
  height: 47px;
  y: 0.5px;
}

.sound-wave-bar:nth-child(1) { 
  animation-delay: -1.9s;
}

.sound-wave--active .sound-wave-bar-1 {
  animation: equalize-1 2s 0s infinite;
}

.sound-wave--active .sound-wave-bar-2 {
  animation: equalize-2 2s 0s infinite;
}

.sound-wave--active .sound-wave-bar-3 {
  animation: equalize-3 2s 0s infinite;
}

.sound-wave-bar:nth-child(2) { 
  animation-delay: -2s;
}

.sound-wave-bar:nth-child(3) { 
  animation-delay: -2.3s;
}

.sound-wave-bar:nth-child(4) { 
  animation-delay: -2.4s;
}

.sound-wave-bar:nth-child(5) { 
  animation-delay: -2.1s;
}

@keyframes equalize-1 {
  0% {
    height: 11px;
    y: 18.5px;
  }
  10% {
    height: 13px;
    y: 17.5px;
  }
  20% {
    height: 8px;
    y: 20px;
  }
  30% {
    height: 13px;
    y: 17.5px;
  }
  40% {
    height: 16px;
    y: 16px;
  }
  50% {
    height: 8px;
    y: 20px;
  }
  60% {
    height: 4px;
    y: 22px;
  }
  70% {
    height: 10px;
    y: 19px;
  }
  80% {
    height: 20px;
    y: 14px;
  }
  90% {
    height: 13px;
    y: 17.5px;
  }
  100% {
    height: 11px;
    y: 18.5px;
  }
}

@keyframes equalize-2 {
  0% {
    height: 31px;
    y: 8.5px;
  }
  10% {
    height: 33px;
    y: 7.5px;
  }
  20% {
    height: 28px;
    y: 10px;
  }
  30% {
    height: 33px;
    y: 7.5px;
  }
  40% {
    height: 36px;
    y: 6px;
  }
  50% {
    height: 28px;
    y: 10px;
  }
  60% {
    height: 24px;
    y: 12px;
  }
  70% {
    height: 30px;
    y: 9px;
  }
  80% {
    height: 40px;
    y: 4px;
  }
  90% {
    height: 33px;
    y: 7.5px;
  }
  100% {
    height: 31px;
    y: 8.5px;
  }
}

@keyframes equalize-3 {
  0% {
    height: 47px;
    y: 0.5px;
  }
  10% {
    height: 40px;
    y: 4px;
  }
  20% {
    height: 44px;
    y: 2px;
  }
  30% {
    height: 28px;
    y: 10px;
  }
  40% {
    height: 42px;
    y: 3px;
  }
  50% {
    height: 40px;
    y: 4px;
  }
  60% {
    height: 46px;
    y: 1px;
  }
  70% {
    height: 38px;
    y: 5px;
  }
  80% {
    height: 44px;
    y: 2px;
  }
  90% {
    height: 33px;
    y: 3px;
  }
  100% {
    height: 47px;
    y: 0.5px;
  }
}
